/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import TextField from '@mui/material/TextField'
import Autocomplete from '@mui/material/Autocomplete'
import match from 'autosuggest-highlight/match'

import { Taxon } from '../../types'
import { Option, MAX_MATCHES, TaxaOptionStatus, Group } from './util'

export interface SelectableTaxonOption extends Taxon {
  status?: TaxaOptionStatus
}

export interface TaxonomySelectFieldProps {
  className?: string
  disabled?: boolean
  gridArea?: string
  label: React.ReactNode
  onChange: (newId: number | null) => void
  options: SelectableTaxonOption[]
  value: number | null
}

const filterOptions = (options: SelectableTaxonOption[], state) => {
  return options
    .filter(({ name, synonyms }) => {
      const filterableName = [name, ...synonyms].join(' ')
      return match(filterableName, state.inputValue).length > 0
    })
    .slice(0, MAX_MATCHES)
}

export const TaxonomySelectField = ({
  className,
  disabled,
  gridArea,
  label,
  onChange,
  options,
  value,
}: TaxonomySelectFieldProps): JSX.Element => {
  const [text, setText] = useState('')

  const handleTextChange = (e: React.SyntheticEvent, newValue: string) => {
    setText(newValue)
  }

  const handleChoose = (e, taxon?: SelectableTaxonOption | null) => {
    if (taxon) {
      onChange(taxon.id)
    } else {
      onChange(null)
    }
  }

  const isOptionsPresented = text.length >= 3

  const foundValue = options.find((option) => option.id === value) || null

  return (
    <Autocomplete
      className={className}
      css={{ gridArea }}
      // disablePortal
      options={isOptionsPresented || foundValue ? options : []}
      onChange={handleChoose}
      renderInput={(params) => <TextField {...params} label={label} />}
      renderOption={(optionProps, taxon, { inputValue }) => (
        <Option {...optionProps} key={taxon.id} taxon={taxon} inputValue={inputValue} />
      )}
      inputValue={text}
      onInputChange={handleTextChange}
      noOptionsText={
        isOptionsPresented ? (foundValue ? 'Change value to continue taxon search' : 'No taxa found') : '...'
      }
      groupBy={(taxon) => taxon.status || 'default'}
      value={foundValue}
      isOptionEqualToValue={(option, v) => option.id === v.id}
      renderGroup={({ group, ...rest }) => (
        <Group group={group as TaxaOptionStatus} labels={['Currently In Box', 'Not In Box']} {...rest} />
      )}
      getOptionLabel={({ name }) => String(name)} // display value; also used by Autocomplete for array key
      filterOptions={filterOptions}
      disabled={disabled}
      size="small"
    />
  )
}
