import gql from 'graphql-tag'
import { PaginatedRecords, Taxonomy as GraphTaxonomy } from '@namcbugdb/common'

import { Api } from '../api'
import { Taxon } from '../../types'

export const fetchTaxaQuery = gql`
  query ($offset: Int) {
    taxonomy(limit: 1000, offset: $offset, searchTerm: "%") {
      records {
        taxonomyId
        scientificName
        levelId
        levelName
        synonyms
        parentTaxonomyId
        notes
        isTargetEffort
      }
      nextOffset
    }
  }
`

export const fetchTaxa = (api: Api) => async (): Promise<Taxon[]> => {
  const data = await api.queryAllPages<{
    taxonomy: PaginatedRecords<GraphTaxonomy>
  }>(fetchTaxaQuery, {}, { dataProperty: 'taxonomy' })
  const { records } = data.taxonomy

  return records.map(
    ({
      taxonomyId: id,
      scientificName: name,
      levelId,
      levelName,
      parentTaxonomyId: parentId,
      synonyms: synonymsJson,
      isTargetEffort,
    }) => ({
      id,
      name,
      levelId,
      levelName,
      levelRankOrder: -1, // TODO: do something instead of hacking this (re-type as optional, or mod type)
      parentId,
      synonyms: synonymsJson ? JSON.parse(synonymsJson) : [],
      isTargetEffort,
    })
  )
}
