import gql from 'graphql-tag'

import { Api, ApiError } from '../api'
import { InvalidateFn } from '../selectors'

export const acceptPendingBoxMutation = gql`
  mutation ($boxId: Int) {
    acceptPendingBox(boxId: $boxId)
  }
`

export const acceptPendingBox =
  (api: Api, invalidate: InvalidateFn) =>
  async ({ boxId }: { boxId: number }): Promise<true> => {
    const data = await api.mutation<{
      acceptPendingBox: number | null // TODO: what?
    }>(acceptPendingBoxMutation, { boxId })

    if (!data.acceptPendingBox) throw new ApiError('submission did not update')

    invalidate('boxes')
    return true
  }
