/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { useRecoilValue } from 'recoil'
import MuiButton from '@mui/material/Button'
import MuiDialog from '@mui/material/Dialog'
import MuiDialogActions from '@mui/material/DialogActions'
import MuiDialogContent from '@mui/material/DialogContent'
// import MuiSkeleton from '@mui/material/Skeleton'

import { Site } from '../../types'
import { ReadOnlyField } from '../ReadOnlyField'
import { withSuspense } from '../withSuspense'
import { NumberField } from '../NumberField'
import { PinSvg } from '../SitesMap'
import { TextField } from '../TextField'
// import { ReadOnlyUsStateField } from './ReadOnlyUsStateField'
import { AutocompleteField } from '../AutocompleteField'
import { useUpdatableState } from '../../lib/useUpdatableState'
import { lookupListToOptions, selectEcosystems, selectSystems, selectWaterbodyTypes } from '../../data/selectors'
import { useNotifications } from '../AppNotifications'

export interface EditSiteDialogProps {
  disallowNames: string[]
  isOpen: boolean
  onClose: () => void
  onSave: (updatedSite: Site) => Promise<void>
  site: Site
}

interface Fields {
  name: string
  latitude: number | null
  longitude: number | null
  systemId: number | null
  ecosystemId: number | null
  waterbodyName: string
  waterbodyTypeId: number | null
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

interface State {
  editMode: 'started' | 'stopping'
  isDirty: boolean
  values: Fields
}

const getInitialState = ({ site }: Pick<EditSiteDialogProps, 'site'>): State => {
  const {
    name,
    latitude,
    longitude,
    systemId = null,
    ecosystemId = null,
    waterbodyName = '',
    waterbodyTypeId = null,
  } = site
  return {
    editMode: 'started',
    isDirty: false,
    values: { name, latitude, longitude, systemId, ecosystemId, waterbodyName, waterbodyTypeId },
  }
}

export const EditSiteDialog = withSuspense(
  ({ disallowNames, isOpen, onClose, onSave, site }: EditSiteDialogProps): JSX.Element => {
    const notifications = useNotifications()

    // SITES AND OPTIONS

    const systemOptions = lookupListToOptions(useRecoilValue(selectSystems))
    const ecosystemOptions = lookupListToOptions(useRecoilValue(selectEcosystems))
    const waterbodyTypeOptions = lookupListToOptions(useRecoilValue(selectWaterbodyTypes))

    // STATE

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [state, setState, updateState] = useUpdatableState<State>(getInitialState({ site }), [site])

    const {
      values: { name, latitude, longitude, systemId, ecosystemId, waterbodyName, waterbodyTypeId },
      isDirty,
      editMode,
    } = state

    const disallowNamesLowercase = useMemo(
      () => disallowNames.map((disallowName) => disallowName.toLowerCase()),
      [disallowNames]
    )

    const errors: FieldErrors = (() => {
      const checkName = name.trim().toLowerCase()
      const result = {
        name:
          checkName !== site.name.toLowerCase() &&
          (!checkName || !!disallowNamesLowercase.find((disallowName) => disallowName === checkName)),
        latitude: !latitude,
        longitude: !longitude,
      }
      if (!Object.values(result).some((value) => value)) return undefined
      return result
    })()

    // HANLDERS

    const setFieldValue = (fieldName: keyof Fields, value: unknown) => {
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
        draft.isDirty = true
      })
    }
    const makeFieldChangeHandler = (fieldName: keyof Fields) => (value: unknown) => setFieldValue(fieldName, value)

    const handleSave = async () => {
      try {
        updateState((draft) => {
          draft.editMode = 'stopping'
        })
        await onSave({
          id: site.id,
          usStateId: 0, // SiteDetailPanel will fetch based on coordinates
          name: name.trim(),
          latitude: latitude ?? 0,
          longitude: longitude ?? 0,
          systemId,
          ecosystemId,
          waterbodyName: waterbodyName.trim(),
          waterbodyTypeId,
        })
        onClose()
      } catch (err) {
        notifications.error(`Failed to save: ${err}`)
        updateState((draft) => {
          draft.editMode = 'started'
        })
      }
    }

    const isSaving = editMode === 'stopping'

    return (
      <MuiDialog open={isOpen} onClose={onClose} fullWidth maxWidth="sm">
        {/* <MuiDialogTitle>Edit Site</MuiDialogTitle> */}
        <MuiDialogContent>
          <div
            css={{
              marginTop: '1em',
              display: 'grid',
              gridTemplateColumns: '1fr 1fr',
              gridTemplateRows: `
              auto
              1em
              auto
              auto
              1em
              auto
              auto
              `,
              gap: '0.5em',
              gridTemplateAreas: `
              "name          name"
              ".             ."
              "usState       id"
              "latitude      longitude"
              ".             ."
              "system        ecosystem"
              "waterbodyName waterbodyType"
            `,
            }}
          >
            <div css={{ gridArea: 'name', display: 'flex', alignItems: 'center' }}>
              <div
                css={{
                  width: 20,
                  marginRight: '0.5em',
                }}
              >
                <PinSvg />
              </div>
              <TextField
                label="Site Name"
                InputProps={{ sx: { fontWeight: 'bold', fontSize: '120%' } }}
                value={name}
                onChange={makeFieldChangeHandler('name')}
                error={errors?.name}
                fullWidth
              />
            </div>
            {/* <ReadOnlyUsStateField gridArea="usState" label="US State" point={value} /> */}
            <ReadOnlyField gridArea="id" label="ID" value="TBD" />
            <NumberField
              gridArea="latitude"
              label="Latitude"
              value={latitude}
              onChange={makeFieldChangeHandler('latitude')}
              step={0.0001}
              error={errors?.latitude}
              min={-90}
              max={90}
            />
            <NumberField
              gridArea="longitude"
              label="Longitude"
              value={longitude}
              onChange={makeFieldChangeHandler('longitude')}
              step={0.0001}
              error={errors?.longitude}
              min={-180}
              max={180}
            />
            <AutocompleteField<number>
              gridArea="system"
              label="System"
              value={systemId ?? null}
              onChange={makeFieldChangeHandler('systemId')}
              options={systemOptions}
              isClearable
            />
            <AutocompleteField<number>
              gridArea="ecosystem"
              label="Ecosystem"
              value={ecosystemId ?? null}
              onChange={makeFieldChangeHandler('ecosystemId')}
              options={ecosystemOptions}
              isClearable
            />
            <TextField
              gridArea="waterbodyName"
              label="Waterbody"
              value={waterbodyName || ''}
              onChange={makeFieldChangeHandler('waterbodyName')}
            />
            <AutocompleteField<number>
              gridArea="waterbodyType"
              label="Waterbody Type"
              value={waterbodyTypeId ?? null}
              onChange={makeFieldChangeHandler('waterbodyTypeId')}
              options={waterbodyTypeOptions}
              isClearable
            />
            {/* <ReadOnlyField gridArea="sampleCount" label="# Samples" value={sampleCount} /> */}
          </div>
        </MuiDialogContent>
        <MuiDialogActions>
          <MuiButton onClick={onClose}>Cancel</MuiButton>
          <MuiButton onClick={handleSave} disabled={!isDirty || !!errors || isSaving}>
            Save
          </MuiButton>
        </MuiDialogActions>
      </MuiDialog>
    )
  },
  () => null
)
