/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import { Switch, Route, Redirect, useHistory } from 'react-router-dom'
import { atom, useSetRecoilState, useRecoilValue } from 'recoil'
import log from 'loglevel'

import { AppHeader } from '../AppHeader'
import { ErrorBoundary } from '../ErrorBoundary'
import { FullScreenWait } from '../FullScreenWait'
import { SideNavBar } from '../SideNavBar'
import { selectHasUser } from '../../data/selectors'
import { withSuspense } from '../withSuspense'

import { BoxDetail } from '../BoxDetail'
import { BoxesListing } from '../BoxesListing'
import { BoxTaxonomy } from '../BoxTaxonomy'
import { ChainOfCustody } from '../ChainOfCustody'
import { CustomerSubmission } from '../CustomerSubmission'
import { ModelsListing } from '../ModelsListing'
import { NewUserDetail, UserDetail, MyUserDetail } from '../UserDetail'
import { PendingBoxReview } from '../PendingBoxReview'
import { PredictorsListing } from '../PredictorsListing'
import { ProjectDetail } from '../ProjectDetail'
import { ProjectsListing } from '../ProjectsListing'
import { SampleDetail } from '../SampleDetail'
import { SampleSorting } from '../SampleSorting'
import { SampleTaxonomyIdentification } from '../SampleTaxonomyIdentification'
import { SitesListing } from '../SitesListing'
import { TranslationsListing } from '../TranslationsListing'
import { UsersListing } from '../UsersListing'

interface NavigateFn {
  boxDetail: (params: { boxId: number }, replace?: boolean) => void
  boxes: () => void
  boxReview: (params: { boxId: number }) => void
  boxSubmission: () => void
  boxTaxonomy: (params: { boxId: number }) => void
  chainOfCustodyForm: (params: { boxId: number }) => void
  home: () => void
  models: () => void
  predictors: () => void
  profile: () => void
  projectCreate: () => void
  projectDetail: (params: { projectId: number }, replace?: boolean) => void
  projects: () => void
  sampleDetail: (params: { sampleId: number }) => void
  sampleSorting: (params: { sampleId: number }) => void
  sampleTaxonomyIdentification: (params: { sampleId: number }) => void
  siteDetail: (params: { siteId: number }, replace?: boolean) => void
  sites: (params?, replace?: boolean) => void
  translations: () => void
  userDetail: (params: { userId: number }) => void
  users: () => void
}

const makeNavigateFn = ({
  history,
  openNewWindowTo,
}: {
  history: { push: (url: string) => void; replace: (url: string) => void }
  openNewWindowTo: (url: string) => void
}): NavigateFn => ({
  boxDetail: ({ boxId }, replace?) => history[replace ? 'replace' : 'push'](`/boxes/${boxId}`),
  boxes: () => history.push('/boxes'),
  boxReview: ({ boxId }): void => history.push(`/boxes/${boxId}/review`),
  boxSubmission: () => history.push(`/boxes/new`),
  boxTaxonomy: ({ boxId }) => history.push(`/boxes/${boxId}/taxonomy`),
  chainOfCustodyForm: ({ boxId }) => openNewWindowTo(`/forms/custody/${boxId}`),
  home: () => history.push('/'),
  models: () => history.push('/models'),
  predictors: () => history.push('/predictors'),
  profile: () => history.push('/profile'),
  projectCreate: () => history.push(`/projects/new`),
  projectDetail: ({ projectId }, replace?) => history[replace ? 'replace' : 'push'](`/projects/${projectId}`),
  projects: () => history.push('/projects'),
  sampleDetail: ({ sampleId }) => history.push(`/samples/${sampleId}`),
  sampleSorting: ({ sampleId }) => history.push(`/samples/${sampleId}/sorting`),
  sampleTaxonomyIdentification: ({ sampleId }) => history.push(`/samples/${sampleId}/taxonomy`),
  siteDetail: ({ siteId }, replace?) => history[replace ? 'replace' : 'push'](`/sites/${siteId}`),
  sites: (params?, replace?) => history[replace ? 'replace' : 'push']('/sites'),
  translations: () => history.push('/translations'),
  userDetail: ({ userId }) => history.push(`/users/${userId}`),
  users: () => history.push('/users'),
})

export const navigateState = atom({
  key: 'AppRouter/navigate',
  default: makeNavigateFn({
    history: { push: (url) => log.debug('history.push:', url), replace: (url) => log.debug('history.replace:', url) },
    openNewWindowTo: (url) => log.debug('new window:', url),
  }),
})

export const useNavigate = (): NavigateFn => useRecoilValue(navigateState)

const isFormPath = (pathname: string) => pathname.includes('/forms/')

export const AppRouter = withSuspense(
  (): JSX.Element => {
    const hasUser = useRecoilValue(selectHasUser)

    const setNavigate = useSetRecoilState(navigateState)
    const history = useHistory()

    const [formLayout, setFormLayout] = useState(isFormPath(history.location.pathname))

    useEffect(() => {
      setNavigate(() =>
        makeNavigateFn({
          history,
          openNewWindowTo: (url) => window.open(window.location.href.replace(/#.*/, `#${url}`)),
        })
      )
    }, [])

    useEffect(() => {
      return history.listen(({ pathname }) => {
        setFormLayout(isFormPath(pathname))
      })
    }, [])

    if (formLayout)
      return (
        <Switch>
          <Route
            exact
            path="/forms/custody/:boxId"
            render={({ match }) => <ChainOfCustody boxId={match.params.boxId} autoPrint />}
          />
          <Route path="*">
            <h2>Page not found.</h2>
          </Route>
        </Switch>
      )

    return (
      <div css={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
        <AppHeader />
        <SideNavBar />
        <div css={{ flex: 1, padding: '0.5em' }}>
          <ErrorBoundary>
            <Switch>
              {!hasUser && (
                <Route path="*">
                  <NewUserDetail />
                </Route>
              )}
              <Route exact path="/">
                <Redirect to="/boxes" />
              </Route>
              <Route exact path="/boxes" render={() => <BoxesListing />} />
              <Route exact path="/boxes/new" render={() => <CustomerSubmission />} />
              <Route exact path="/boxes/:boxId" render={({ match }) => <BoxDetail boxId={match.params.boxId} />} />
              <Route
                exact
                path="/boxes/:boxId/taxonomy"
                render={({ match }) => <BoxTaxonomy boxId={match.params.boxId} />}
              />
              <Route
                exact
                path="/boxes/:boxId/review"
                render={({ match }) => <PendingBoxReview boxId={match.params.boxId} />}
              />
              <Route
                exact
                path="/samples/:sampleId"
                render={({ match }) => <SampleDetail sampleId={match.params.sampleId} />}
              />
              <Route
                exact
                path="/samples/:sampleId/sorting"
                render={({ match }) => <SampleSorting sampleId={match.params.sampleId} />}
              />
              <Route
                exact
                path="/samples/:sampleId/taxonomy"
                render={({ match }) => <SampleTaxonomyIdentification sampleId={match.params.sampleId} />}
              />
              <Route exact path="/models" render={() => <ModelsListing />} />
              <Route exact path="/predictors" render={() => <PredictorsListing />} />
              <Route exact path="/projects" render={() => <ProjectsListing />} />
              <Route exact path="/projects/new" render={() => <ProjectDetail isNew />} />
              <Route
                exact
                path="/projects/:projectId"
                render={({ match }) => <ProjectDetail projectId={match.params.projectId} />}
              />
              <Route
                exact
                path="/sites/:siteId?"
                render={({ match }) => <SitesListing siteId={match.params.siteId} />}
              />
              <Route exact path="/translations" render={() => <TranslationsListing />} />
              <Route exact path="/users" render={() => <UsersListing />} />
              <Route exact path="/users/:userId" render={({ match }) => <UserDetail userId={match.params.userId} />} />
              <Route exact path="/profile" render={() => <MyUserDetail />} />
              <Route path="*">
                <h2>Page not found.</h2>
              </Route>
            </Switch>
          </ErrorBoundary>
        </div>
      </div>
    )
  },
  () => <FullScreenWait>Retrieving profile...</FullScreenWait>
)
