import gql from 'graphql-tag'
import { Predictor as GraphPredictor } from '@namcbugdb/common'

import { Api } from '../api'
import { Predictor } from '../../types'

export const fetchPredictorsQuery = gql`
  query ($offset: Int!) {
    predictors(limit: 500, offset: $offset) {
      records {
        predictorId
        predictorName
        predictorType
        abbreviation
        units
        isTemporal
        modelCount
      }
      nextOffset
    }
  }
`

export const fetchPredictors = (api: Api) => async (): Promise<Predictor[]> => {
  const data = await api.queryAllPages<{ predictors: { records: GraphPredictor[] } }>(
    fetchPredictorsQuery,
    {},
    { dataProperty: 'predictors' }
  )

  return data.predictors.records.map(({ predictorId: id, predictorName: name, predictorType: type, ...rest }) => ({
    id,
    name,
    type,
    ...rest,
  }))
}
