export const pluralize = (qty: number | null | undefined, word: string): string => {
  const isOne = typeof qty === 'number' && qty === 1

  switch (word) {
    case 'is':
      return isOne ? 'is' : 'are'
    case 'box':
      return isOne ? 'box' : 'boxes'
    case 'taxon':
      return isOne ? 'taxon' : 'taxa'
    case 'taxa':
      return 'taxa'
    case 'individuals counted':
      return word // do not change
    default:
      return isOne ? word : `${word}s`
  }
}
