/** @jsxImportSource @emotion/react */
import React from 'react'
import { atom, useRecoilState } from 'recoil'
import MuiAccordion from '@mui/material/Accordion'
import MuiAccordionSummary from '@mui/material/AccordionSummary'
import MuiAccordionDetails from '@mui/material/AccordionDetails'
import Skeleton from '@mui/material/Skeleton'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'

import { Sample } from '../../types'
import { formatUtcDateTimeToAppDate } from '../../lib/formats'

const isExpandedState = atom<boolean>({
  key: 'SampleInfoPanel/isExpanded',
  default: true,
})

interface SampleInfoPanelProps {
  sample: Sample
  actions?: React.ReactNode
}

const GridInfoField = ({ name, label, value }: { name: string; label: string; value: React.ReactNode }) => (
  <>
    <div css={{ gridArea: `${name}Label` }}>{label}:</div>
    <div css={{ gridArea: `${name}Value` }}>{value}</div>
  </>
)

export const SampleInfoPanel = ({ sample, actions = null }: SampleInfoPanelProps): JSX.Element => {
  const [isExpanded, setIsExpanded] = useRecoilState(isExpandedState)

  if (!sample) return <Skeleton variant="rectangular" />

  const { id, boxId, sorterName = '', sampleDate, sortEndDate, customerName, siteName } = sample

  return (
    <MuiAccordion
      expanded={isExpanded}
      onChange={(e, newValue) => setIsExpanded(newValue)}
      css={{ marginBottom: '1em' }}
    >
      <MuiAccordionSummary expandIcon={<ExpandMoreIcon />} css={{ display: 'flex' }}>
        <span css={{ alignSelf: 'center', fontSize: '125%' }}>
          <span css={{ fontWeight: 'bold' }}>Sample {id}</span> (Box {boxId})
        </span>
        {actions && (
          <div
            css={{ margin: '0 0.5em 0 auto' }}
            onClick={(e) => {
              e.stopPropagation()
            }}
          >
            {actions}
          </div>
        )}
      </MuiAccordionSummary>
      <MuiAccordionDetails
        css={{
          display: 'grid',
          gap: '0.75em',
          gridTemplateColumns: 'repeat(4, auto) 1fr',
          gridTemplateAreas: `
          "sampleDateLabel sampleDateValue . . ."
            "sorterLabel sorterValue sortDateLabel sortDateValue ."
            "customerLabel customerValue siteLabel siteValue ."
        `,
        }}
      >
        <GridInfoField name="sorter" label="Sorter" value={sorterName} />
        <GridInfoField name="sortDate" label="Sort Date" value={formatUtcDateTimeToAppDate(sortEndDate)} />
        <GridInfoField name="sampleDate" label="Sample Date" value={formatUtcDateTimeToAppDate(sampleDate)} />
        <GridInfoField name="customer" label="Customer" value={customerName} />
        <GridInfoField name="site" label="Site" value={siteName} />
      </MuiAccordionDetails>
    </MuiAccordion>
  )
}
