import gql from 'graphql-tag'
import { BoxInfo as GraphBoxInfo } from '@namcbugdb/common'

import { Api } from '../api'
import { Box } from '../../types'
import { transformBox } from './fetchBoxes'
import { deNull } from '../../lib/deNull'

export const fetchBoxDetailByIdQuery = gql`
  query ($boxId: Int!) {
    boxInfo(boxId: $boxId) {
      boxId
      boxState
      boxStateId
      customerId
      customer {
        organizationName
      }
      submittedBy
      createdDate
      boxReceivedDate
      processingCompleteDate
      measurements
      midges
      taxaQa
      sorterQa
      description
      sampleCount
      sortComplete
      idComplete
      alias
      modelIds
      submissionCount
    }
  }
`

export const fetchBoxDetailById =
  (api: Api) =>
  async (boxId: number): Promise<Box | null> => {
    const data = await api.query<{ boxInfo: GraphBoxInfo }>(fetchBoxDetailByIdQuery, { boxId })
    if (!data.boxInfo) return null
    const {
      customer: { organizationName: customerName },

      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      submissions, // not queried, discard for typing incompatibility

      ...restBoxInfo
    } = data.boxInfo
    return deNull(transformBox({ ...restBoxInfo, customerName }), {
      description: '',
      modelIds: [],
    })
  }
