/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { selectTranslations } from '../../data/selectors'
import { DataGrid } from '../DataGrid'
import { Translation } from '../../types'
import { withSuspense } from '../withSuspense'
import { translationsColumnDefs } from './columnDefs'
import { Title } from '../Title'

export const TranslationsListing = withSuspense(
  () => {
    const translations = useRecoilValue(selectTranslations)
    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ flex: 1 }}>
          <Title>Translations</Title>
          <DataGrid<Translation> rows={translations} columnDefs={translationsColumnDefs} />
        </div>
      </div>
    )
  },
  () => <Skeleton height="20em" />
)
