import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '../api'
import { Sample } from '../../types'
import { deNull } from '../../lib/deNull'

export const fetchSampleByIdQuery = gql`
  query ($sampleId: Int!) {
    samples(sampleIds: [$sampleId]) {
      records {
        area
        boxId
        boxState
        customerName
        customerSiteCode
        designTypeId
        ecosystemId
        effortTypeId
        elutriation
        fieldNotes
        fieldSplit
        habitatId
        idEndDate # -> identificationEndDate
        idErId # -> identifierId
        idErName # -> identifierName
        idStartDate # -> identificationStartDate
        jarCount
        labNotes
        labSplit
        measurements
        mesh
        sampleDate
        sampleId # -> id
        sampleLatitude
        sampleLongitude
        sampleMethodId
        sampleType # -> sampleTypeName
        siteId
        siteName
        sortEndDate
        sorterCount
        sorterId
        sorterName
        sortStartDate
        sortTime
        submittedBy # -> submittedByName
        usState
        visitId
      }
    }
  }
`

export const fetchSampleById =
  (api: Api) =>
  async (sampleId: number): Promise<Sample | null> => {
    const data = await api.query<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchSampleByIdQuery, { sampleId })

    const sample = data.samples.records[0]
    if (!sample) return null

    const {
      idEndDate: identificationEndDate,
      idErId: identifierId,
      idErName: identifierName,
      idStartDate: identificationStartDate,
      sampleId: id,
      sampleType: sampleTypeName,
      submittedBy: submittedByName,
      ...restSample
    } = sample

    return deNull(
      {
        ...restSample,
        identificationEndDate,
        identifierId,
        identifierName,
        identificationStartDate,
        id,
        sampleTypeName,
        submittedByName,
      },
      {
        customerSiteCode: '',
        fieldNotes: '',
        labNotes: '',
        visitId: '',
      }
    )
  }
