/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { selectPredictors } from '../../data/selectors'
import { DataGrid } from '../DataGrid'
import { Predictor } from '../../types'
import { withSuspense } from '../withSuspense'
import { predictorsColumnDefs } from './columnDefs'
import { Title } from '../Title'

export const PredictorsListing = withSuspense(
  () => {
    const predictors = useRecoilValue(selectPredictors)
    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ flex: 1 }}>
          <Title>Predictors</Title>
          <DataGrid<Predictor> rows={predictors} columnDefs={predictorsColumnDefs} />
        </div>
      </div>
    )
  },
  () => <Skeleton height="20em" />
)
