/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { selectModels } from '../../data/selectors'
import { DataGrid } from '../DataGrid'
import { Model } from '../../types'
import { withSuspense } from '../withSuspense'
import { modelsColumnDefs } from './columnDefs'
import { Title } from '../Title'

export const ModelsListing = withSuspense(
  () => {
    const models = useRecoilValue(selectModels)
    return (
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ flex: 1 }}>
          <Title>Models</Title>
          <DataGrid<Model> rows={models} columnDefs={modelsColumnDefs} />
        </div>
      </div>
    )
  },
  () => <Skeleton height="20em" />
)
