/** @jsxImportSource @emotion/react */
import React from 'react'

import { formatNumber } from '../../lib/formats'
import { pluralize } from '../../lib/pluralize'
import { LinearProgress } from '../LinearProgress'

interface DataGridRecordCountProps {
  className?: string
  descriptor: string
  isLoading?: boolean
  total: number
  visible: number
}

export const DataGridRecordCount = ({
  className,
  descriptor,
  isLoading,
  total,
  visible,
}: DataGridRecordCountProps): JSX.Element => {
  return (
    <div
      className={className}
      css={{
        fontSize: '0.8125rem' /* match chip */,
        display: 'flex',
        alignItems: 'center',
        position: 'relative',
      }}
    >
      {isLoading && <LinearProgress css={{ position: 'absolute', width: '100%', bottom: 5 }} />}
      {total === visible
        ? `${formatNumber(total)} ${pluralize(total, descriptor)}`
        : `${formatNumber(visible)} of ${formatNumber(total)} ${pluralize(visible, descriptor)} shown`}
    </div>
  )
}

interface DataGridHeadingProps {
  children?: React.ReactNode
}

export const DataGridHeading = ({ children }: DataGridHeadingProps): JSX.Element => (
  <div css={{ fontWeight: '500', marginLeft: '1em' }}>{children}</div>
)
